/* AuthStyles.css */

/* Container style */
.form-container {
  margin-top: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 70vh;
  flex-direction: column;
  background: linear-gradient(0deg, #f0f0f0 0%, #d9d9d9 100%);
}

/* Form style */
.form-container form {
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.35);
  padding: 20px;
  background-color: #fff;
  border-radius: 10px;
  width: 100%;
  max-width: 400px;
}

/* Form title style */
.form-container form .title {
  text-align: center;
  margin-bottom: 15px;
  font-weight: bold;
  font-family: "Playfair Display", serif;
  font-size: 24px;
}

/* Input field style */
.form-container form input {
  border: none;
  border-bottom: 1px solid #000;
  border-radius: 0;
  padding: 10px;
  margin-bottom: 15px;
  width: 100%;
  font-size: 16px;
}

/* Button style */
.form-container form button {
  border: none;
  border-radius: 0;
  background-color: #000;
  color: white;
  width: 100%;
  padding: 10px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.form-container form button:hover {
  background: linear-gradient(to right, #434343, #000000);
}

/* Forgot password button style */
.forgot-btn {
  background-color: #434343a4 !important;
  border: none !important;
  color: white !important;
  font-size: 14px;
  padding: 5px 0;
  cursor: pointer;
}

.forgot-btn:hover {
  background-color: #434343 !important;
}

/* Responsive styles */
@media screen and (max-width: 768px) {
  .form-container form {
    max-width: 100%;
  }
}