/* Cart Page Container */
.cart-page {
  margin-top: 64px;
  background-color: #f9f9f9;
  /* Light gray background color for the page */
  padding: 20px;
  /* Added padding for spacing */
}

/* Page Title Styles */
.cart-page h1 {
  padding: 15px !important;
  font-family: "Roboto", sans-serif;
  font-weight: bold;
  /* Made the title bold for emphasis */
  background: linear-gradient(to right, #ffd9009e, #69e75399) !important;
  /* Professional blue background color for h1 */
  color: black;
  /* White text color for better contrast */
  border-radius: 8px;
  /* Rounded corners for a softer look */
  text-align: center;
  margin-bottom: 20px;
}

/* Subtitle Styles */
.cart-page h1>p {
  font-size: 20px;
  margin-top: 10px;
  text-align: center;
  color: black;
  /* Dark gray text color for subtlety */
}

/* Cart Item Styles */
.cart-page .card {
  width: 99%;
  padding: 15px;
  /* Increased padding for better spacing and readability */
  height: auto;
  /* Removed fixed height to accommodate variable content */
  margin-bottom: 15px;
  /* Increased margin for better separation between items */
  background-color: #fff;
  /* White background for card items */
  border: 1px solid #e0e0e0;
  /* Added a subtle border for separation */
  border-radius: 8px;
  /* Rounded corners for a cleaner look */
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
  /* Subtle box shadow for depth */
}

/* Remove Button Styles */
.cart-page .cart-remove-btn button {
  width: 100%;
  background-color: #e74c3c;
  /* Decent red background color for remove buttons */
  color: white;
  border: none;
  border-radius: 5px;
  padding: 8px 12px;
  /* Increased padding for better clickability */
  cursor: pointer;
  transition: background-color 0.3s;
  /* Hover effect transition */
}

.cart-page .cart-remove-btn button:hover {
  background-color: #c0392b;
  /* Darker red on hover for visual feedback */
}

/* Cart Summary Styles */
.cart-page .cart-summary {
  text-align: center;
  /* Align summary text to the center */
  background-color: #fff;
  /* White background for summary */
  padding: 20px;
  /* Increased padding for spacing */
  border-radius: 8px;
  /* Rounded corners for a cleaner look */
  margin-top: 20px;
  /* Adjusted margin for better spacing */
  border: 1px solid #e0e0e0;
  /* Added a subtle border for separation */
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
  /* Subtle box shadow for depth */
}

/* Cart Summary Title Styles */
.cart-page .cart-summary h2 {
  font-size: 24px;
  /* Increased font size for emphasis */
  margin-bottom: 15px;
  /* Improved spacing between elements */
  color: #333;
  /* Dark gray text color for emphasis */
}

/* Cart Summary Item Styles */
.cart-page .cart-summary p {
  margin-bottom: 10px;
  /* Improved spacing between summary items */
  color: #555;
  /* Dark gray text color for subtlety */
}

/* Cart Summary Divider Styles */
.cart-page .cart-summary hr {
  border: 1px solid #e0e0e0;
  /* Added a subtle divider for separation */
  margin-top: 20px;
  /* Improved spacing between elements */
}

/* Button Styles */
.cart-page .btn-outline-warning,
.cart-page .btn-primary {
  width: 100%;
  padding: 10px;
  font-weight: bold;
  border-radius: 5px;
  cursor: pointer;
}

.cart-page .btn-outline-warning {
  background-color: #f1c40f;
  /* Decent yellow background color for warning buttons */
  color: #333;
  border: 2px solid #f1c40f;
  transition: background-color 0.3s;
  /* Hover effect transition */
}

.cart-page .btn-outline-warning:hover {
  background-color: #d4ac0d;
  /* Darker yellow on hover for visual feedback */
}

.cart-page .btn-primary {
  background-color: #27ae60;
  /* Decent green background color for primary buttons */
  border: none;
  color: white;
  transition: background-color 0.3s;
  /* Hover effect transition */
}

.cart-page .btn-primary:hover {
  background-color: #218f54;
  /* Darker green on hover for visual feedback */
}